<template>
  <div class="hotelList">
    <div class="drop-shadow-down" :class="toppest ? '' : 'show'"></div>
    <div v-if="!loadingHotelList" class="pageSelectorButton">검색 결과 {{ this.totalCount }}개</div>
    <ul v-if="exceptSoldOutList.length > 0" ref="hotelList">
      <li
        v-for="(x, index) in cutHotel"
        :key="x.hotelId"
        @click="submit(x)"
      >
        <button class="wishBtn" @click.stop="addWish"></button>
        <div class="imageBack">
          <swiper-image
            :imageIndex="index"
            :images="x.imageUrls"
            @image-index-change="imageIndexChange"
          ></swiper-image>
          <div class="hotelDesc">
            <div>{{ x.name }}</div>
            <div>{{ x.address }}</div>
            <div>
              <span class="starWrapper" v-if="x.star"><span class="star" v-for="y in parseInt(x.star, 10)" :key="y"></span></span>
              <span v-if="x.rating"><span v-if="!x.numberOfReviews || !x.star">평점: </span>{{ x.rating * 2 }}</span>
              <span v-if="x.numberOfReviews" class="thin"> ({{ format.numberFormat(x.numberOfReviews) }}개 리뷰)</span>
            </div>
          </div>
        </div>
        <button class="imgMore"
          v-if="Array.isArray(x.imageUrls) && x.imageUrls.length > 1"
          @click.stop="openImagePop(index)"
        >{{ setImageUrl[index] ? setImageUrl[index] + 1 : arrImageIndex[index] + 1 }}/{{ x.imageUrls.length }}</button>
        <div class="btnBack">
          <button class="submit" v-if="x.rooms && x.rooms.length > 0" :class="promotion ? '' : 'noPromotion'">
            <div>{{ promotion }}</div>
            <div class="roboto">{{ format.numberFormat(parseInt(x.rooms[0].totalPrice, 10)) }}</div>
            <div>1박평균 <span class="roboto">{{ format.numberFormat(parseInt(x.rooms[0].pricePerNight, 10)) }}</span></div>
            <div>가격비교</div>
          </button>
        </div>
      </li>
      <div class="pageLoading" v-if="loadingHotelList"></div>
    </ul>
    <div v-else-if="loadingHotelList">
      <hotel-loading></hotel-loading>
    </div>
    <div class="listNoti" v-else>검색한 도시/날짜에<br>머물 수 있는 호텔이 없습니다.</div>
    <div class="drop-shadow-up" :class="endBottom ? '' : 'show'"></div>
    <button @click="goTop" :class="toppest ? '' : 'show'" class="topButton"></button>
  </div>
</template>

<script>
import HotelLoading from './HotelLoading'
import Format from '../../util/format'
import SwiperImage from './SwiperImage'

export default {
  components: {
    HotelLoading,
    SwiperImage
  },
  props: ['exceptSoldOutList', 'sortedList', 'hotelIndex', 'inputData', 'loadingHotelList', 'popping', 'totalCount', 'mid', 'scrollContentNum', 'lastScrollTop'],
  data () {
    return {
      format: Format,
      setImageUrl: {},
      endBottom: false,
      toppest: true,
      promotion: ''
    }
  },
  computed: {
    arrImageIndex () {
      const arr = []
      for (let x = 0; x < this.sortedList.length; x += 1) {
        arr.push(0)
      }
      return arr
    },
    cutHotel () {
      const arr = []
      for (let x = 0; x < this.sortedList.length && x < this.scrollContentNum; x += 1) {
        arr.push(this.exceptSoldOutList[x])
      }
      return arr
    }
  },
  methods: {
    openPop (value) {
      this.$emit('open-pop', value)
    },
    submit (value) {
      this.$emit('submit', value)
    },
    changeScrollTop () {
      if (!this.loadingHotelList) {
        this.$emit('last-scroll-top-change', window.pageYOffset)
        if (window.pageYOffset >= this.$refs.hotelList.clientHeight - window.innerHeight + 118) {
          if (this.loadingHotelList) {
            return
          }
          if (this.scrollContentNum >= this.exceptSoldOutList.length) {
            this.$emit('get-hotel-list')
          } else {
            this.$emit('scroll-content-num-increase')
          }
          this.endBottom = true
        } else {
          this.endBottom = false
        }
        if (window.pageYOffset === 0) {
          this.toppest = true
        } else {
          this.toppest = false
        }
      }
    },
    imageIndexChange (imageIndex, index) {
      this.$set(this.setImageUrl, imageIndex, index)
    },
    goTop () {
      window.scrollTo(0, 0)
    },
    addWish () {
      alert('준비중입니다')
    },
    openImagePop (index) {
      this.$emit('change-scroll-pop-image-index', index)
      this.$emit('open-pop', 'image')
    }
  },
  mounted () {
    window.scrollTo(0, parseInt(this.lastScrollTop, 10))
    window.addEventListener('scroll', this.changeScrollTop)
  },
  destroyed () {
    window.removeEventListener('scroll', this.changeScrollTop)
  }
}
</script>

<style scoped>
  .hotelList{
    position: relative;
    user-select: none;
    padding-top: 70px;
  }
  .listNoti{
    font-size: 16px; padding: 50px; text-align: center; color: #FFF;
    position: fixed;
    top: 118px;
    bottom: 48px;
    left: 0;
    right: 0;
    z-index: 1;
    background-color: #5C6E7C;
  }
  .hotelList > ul{
    background-color: #846b55;
  }
  .hotelList > ul > li{
    position: relative;
  }
  .wishBtn{
    position: absolute;
    top: 20px;
    right: 20px;
    background-image: url(../../assets/new-hart.svg);
    width: 32px;
    height: 32px;
    z-index: 2;
    display: none;
  }
  .hotelList > ul > li > .imageBack > div:first-of-type:after{
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 140px;
    left: 0;
    z-index: 1;
    background-image: linear-gradient(to bottom, rgba(92, 110, 124, 0), #846b55);
    pointer-events: none;
  }
  .imageBack{
    position: relative;
    height: 60vw;
  }
  .hotelList > ul > li .hotelDesc{
    padding: 0 20px;
    color: #FFF;
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    z-index: 3;
    pointer-events: none;
  }
  .hotelDesc{
    padding: 180px 20px 0;
    position: relative;
  }
  .hotelDesc > *{
    color: #FFF;
  }
  .hotelDesc > div:first-of-type {
    font-size: 20px;
    font-weight: 500;
  }
  .hotelDesc > div:nth-of-type(2){
    font-size: 12px;
    letter-spacing: -.3px;
    margin-top: 4px;
    line-height: 17px;
    font-weight: 400;
  }
  .hotelDesc > div:nth-of-type(3){
    margin-top: 7px;
    font-size: 12px;
    letter-spacing: -.3px;
  }
  .hotelDesc > div:nth-of-type(2):before{
    width: 13px;
    height: 15px;
    float: left;
    content: '';
    margin-right: 5px;
    background: url(../../assets/location.svg) no-repeat;
  }
  button.imgMore{
    position: absolute;
    right: 20px;
    top: 60vw;
    margin-top: -17px;
    line-height: 22px;
    height: 22px;
    padding: 0;
    z-index: 3;
    color: #FFF;
    border: 0;
    background-color: transparent;
    font-size: 12px;
  }
  button.imgMore:after{
    content: '';
    display: block;
    width: 22px;
    height: 22px;
    float: right;
    background-image: url(../../assets/plus.svg);
    margin-left: 4px;
  }
  .starWrapper{
    margin-right: 5px;
  }
  .starWrapper:after{
    content: '|';
    color: rgba(255, 255, 255, .3);
    margin-left: 5px;
  }
  .hotelDesc .star{
    margin-right: 6px;
    float: left;
    width: 13px;
    height: 13px;
    background: url(../../assets/star.svg) no-repeat;
  }
  .hotelDesc .star:last-of-type{
    margin-right: 0;
  }
  .hotelDesc .thin{
    font-weight: 400;
  }
  .hotelList > ul > li > .btnBack{
    margin-top: 20px;
    padding: 0 20px;
    min-height: 40px;
  }
  .hotelList > ul > li > .btnBack button.submit{
    width: 100%;
    height: 90px;
    border-radius: 3px;
    background-color: rgba(255, 255, 255, 0.9);
    border: 0;
    margin-bottom: 20px;
    padding: 0;
    position: relative;
  }
  button.submit > div{
    position: absolute;
  }
  button.submit > div:first-of-type{
    padding: 0 4px;
    height: 20px;
    opacity: 0.8;
    background-color: #1a1a1c;
    font-size: 11px;
    font-weight: 600;
    letter-spacing: -0.5px;
    color: #ffffff;
    line-height: 20px;
    left: 20px;
    top: 10px;
  }
  button.submit.noPromotion > div:first-of-type{
    opacity: 0;
  }
  button.submit > div:nth-of-type(2){
    color: #000;
    font-size: 24px;
    top: 32px;
    left: 20px;
  }
  button.submit.noPromotion > div:nth-of-type(2){
    top: 16px;
  }
  button.submit > div:nth-of-type(2):before{
    content: '₩';
    font-family: 'Roboto Slab', sans-serif;
  }
  button.submit > div:nth-of-type(3){
    bottom: 12px;
    left: 20px;
    color: #999;
    font-size: 12px;
  }
  button.submit.noPromotion > div:nth-of-type(3){
    bottom: 24px;
  }
  button.submit > div:nth-of-type(3) > span:before{
    content: '₩';
    font-family: 'Roboto Slab', sans-serif;
  }
  button.submit > div:nth-of-type(4){
    width: 80px;
    height: 30px;
    border-radius: 15px;
    color: #ffffff;
    font-size: 14px;
    line-height: 30px;
    background-color: #ff7e55;
    top: 30px;
    right: 20px;
  }
  .pageLoading{
    position: relative;
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    background-color: #333;
    color: #FFF;
    font-size: 12px;
  }
  @keyframes spinner {
    to {transform: rotate(360deg);}
  }
  .pageLoading:before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 7px;
    width: 16px;
    height: 16px;
    margin-left: -8px;
    border-radius: 50%;
    border: 1px solid #ccc;
    border-top-color: #8F9D9D;
    animation: spinner .6s linear infinite;
  }
  .pageSelectorButton{
    position: fixed;
    z-index: 4;
    left: 20px;
    top: 143px;
    padding: 0 4px;
    height: 22px;
    line-height: 20px;
    text-align: center;
    color: #FFF;
    font-size: 10px;
    transition: .3s ease;
    border-radius: 3px;
    background-color: rgba(0, 0, 0, 0.1);
    border: solid 1px rgba(0, 0, 0, 0.08);
  }
  .topOut .pageSelectorButton{
    transform: translateY(-48px);
  }
  .drop-shadow-up {
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 48px;
    height: 10px;
    opacity: 0;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0,0,0,.6));
    z-index: 5;
    transition: .3s ease;
    pointer-events: none;
  }
  .scrollDown .drop-shadow-up{
    transform: translateY(48px);
  }
  .drop-shadow-up.show{
    opacity: .3;
  }
  .drop-shadow-down {
    width: 100%;
    position: fixed;
    left: 0;
    top: 118px;
    height: 10px;
    opacity: 0;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0), rgba(0,0,0,.6));
    z-index: 5;
    transition: .3s ease;
    pointer-events: none;
  }
  .topOut .drop-shadow-down{
    transform: translateY(-48px);
  }
  .drop-shadow-down.show{
    opacity: .25;
  }
  .topButton{
    width: 45px;
    height: 45px;
    background-color: #1a1a1c;
    border-radius: 100%;
    position: fixed;
    bottom: 60px;
    right: 15px;
    z-index: 5;
    transition: .3s ease;
    background-image: url(../../assets/top.svg);
    background-size: 12px;
    background-repeat: no-repeat;
    background-position: center center;
    opacity: 0;
    pointer-events: none;
  }
  .topButton.show{
    opacity: 1;
    pointer-events: all;
  }
  .scrollDown .topButton{
    transform: translateY(48px);
  }
</style>



