<template>
  <div id="wmpHotel" :class="{
    scrollDown,
    topOut
  } ">
    <Header
      :isLogin="mid ? true : false"
      :historyLength="historyLength"
      :isFromApp="isFromApp"
      @pop-alert="popAlert"
    ></Header>
    <div class="page"
      ref="wmpHotel"
    >
      <transition name="fade" mode="out-in">
        <router-view
          :key="getParamKey"
          :inputData="inputData"
          :hotelList="hotelList"
          :hotelIndex="hotelIndex"
          :filterInfo="filterInfo"
          :sortCondition="sortCondition"
          :listingCondition="listingCondition"
          :mid="mid"
          :isAirRsv="isAirRsv"
          :mapList="mapList"
          :selectedHotel="selectedHotel"
          :popping="popping"
          :lastScrollTop="lastScrollTop"
          :scrollContentNum="scrollContentNum"
          :isFromApp="isFromApp"
          @hotel-index-change="hotelIndexChange"
          @hotel-index-reset="hotelIndexReset"
          @reset-filter-info="resetFilterInfo"
          @change-selected-hotel="changeSelectedHotel"
          @change-list-condition="changeListCondition"
          @last-scroll-top-change="lastScrollTopChange"
          @scroll-content-num-increase="scrollContentNumIncrease"
          @open-pop="openPop"
          @close-pop="closePop"
          @pop-alert="popAlert"
        />
      </transition>
    </div>
    <iframe class="hide" v-if="iswmpapp" :src="wmpLoginUrl"></iframe>
    <iframe class="hide" v-if="$route.name !== 'auth' && !iswmpapp" :src="loginCheckUrl"></iframe>
    <Footer
      :mid="mid"
      :isFromApp="isFromApp"
      @pop-alert="popAlert"
    ></Footer>
    <alert
      v-if="alert.type === 'alert' || (alert.type === 'login' && !mid) || alert.type === 'loginHeader'"
      :alert="alert"
    ></alert>
  </div>
</template>

<script>
import Vue from 'vue'
import Router from 'vue-router'
import Search from './Search/Wrapper'
import HotelList from './HotelList/Wrapper'
import Detail from './Detail/Wrapper'
import Book from './Book/Wrapper'
import VueCookies from 'vue-cookies'
import Header from './Common/Header'
import Footer from './Common/Navigator'
import Error from './Common/Error'
import Oops from './Common/Oops'
import Api from '../api/api'
import Format from '../util/format'
import Alert from './Common/Alert'
// import DeviceInfo from '../util/bp-device-info'

window.EventBus = new Vue()

const router = new Router({
  routes: [
    {
      path: '/:popFlag(destination|date|option)?',
      name: 'search',
      component: Search
    },
    {
      path: '/hotelList/:destination/:checkin/:checkout/:adults/:children/:room/:popFlag(search|map|filter|sort|destination|date|option|image)?',
      name: 'hotelList',
      component: HotelList
    },
    {
      path: '/detail/:destination/:checkin/:checkout/:adults/:children/:room/:hotelKey/:popFlag(image)?',
      name: 'detail',
      component: Detail
    },
    {
      path: '/book',
      name: 'book',
      component: Book
    },
    {
      path: '/oops',
      name: 'oops',
      component: Oops
    },
    {
      path: '/*',
      name: '404',
      component: Error
    }
  ],
  mode: 'history'
})

router.afterEach((to, from) => {
  if (to.query.isStack) { // stack 으로 올린 것들은 중복을 방지하가 위해 return함
    return
  }
  if (window.gtm) {
    window.gtm.push({ event: 'nuxtRoute', pageType: 'PageView', pageUrl: to.fullPath })
  }
})

Vue.use(VueCookies)

export default {
  router,
  components: {
    Header,
    Footer,
    Alert
  },
  data () {
    let inputData = {}
    if (this.$cookies.get('bphotels')) {
      try {
        const inputDataList = JSON.parse(this.$cookies.get('bphotels'))
        if (Array.isArray(inputDataList) && inputDataList.length > 0) {
          inputData = inputDataList[inputDataList.length - 1]
        }
        if (parseInt(inputData.checkin.replace(/-/g, '')) < parseInt(Format.getToday().replace(/-/g, ''))) {
          const today = new Date()
          const tomorrow = new Date()
          tomorrow.setDate(today.getDate() + 1)
          inputData.checkin = `${today.getFullYear()}-${Format.zeros(today.getMonth() + 1)}-${Format.zeros(today.getDate())}`
          inputData.checkout = `${tomorrow.getFullYear()}-${Format.zeros(tomorrow.getMonth() + 1)}-${Format.zeros(tomorrow.getDate())}`
        }
      } catch (e) {
        const today = new Date()
        const tomorrow = new Date()
        tomorrow.setDate(today.getDate() + 1)
        inputData = {
          destination: {
            label: '',
            lat: '',
            lng: '',
            type: '',
            country: '',
            cityName: '',
            cityId: '',
            placeId: '',
            countryName: ''
          },
          checkin: `${today.getFullYear()}-${Format.zeros(today.getMonth() + 1)}-${Format.zeros(today.getDate())}`,
          checkout: `${tomorrow.getFullYear()}-${Format.zeros(tomorrow.getMonth() + 1)}-${Format.zeros(tomorrow.getDate())}`,
          adults: 1,
          children: [],
          room: 1
        }
      }
    } else {
      const today = new Date()
      const tomorrow = new Date()
      tomorrow.setDate(today.getDate() + 1)
      inputData = {
        destination: {
          label: '',
          lat: '',
          lng: '',
          type: '',
          country: '',
          cityName: '',
          cityId: '',
          placeId: '',
          countryName: ''
        },
        checkin: `${today.getFullYear()}-${Format.zeros(today.getMonth() + 1)}-${Format.zeros(today.getDate())}`,
        checkout: `${tomorrow.getFullYear()}-${Format.zeros(tomorrow.getMonth() + 1)}-${Format.zeros(tomorrow.getDate())}`,
        adults: 1,
        children: [],
        room: 1
      }
    }
    return {
      inputData,
      hotelList: {
        param: '',
        searchTime: '',
        list: [],
        totalCount: 0
      },
      hotelIndex: 0,
      filterInfo: {},
      sortCondition: 'BEST',
      mid: false,
      isAirRsv: 'false',
      mapList: [],
      selectedHotel: {},
      listingCondition: 'list',
      loginCheckUrl: Api.isLoginCheck(),
      wmpLoginUrl: Api.wmpLoginUrl(),
      format: Format,
      historyLength: 0,
      scrollDown: false,
      lastScroll: 0,
      topOut: false,
      popping: false,
      scrollContentNum: 0,
      lastScrollTop: 0,
      iswmpapp: false,
      isFromApp: false,
      alert: {
        type: '',
        message: ''
      }
    }
  },
  computed: {
    getParamKey () {
      const key = []
      Object.keys(this.$route.params).forEach((x) => {
        if (x !== 'popFlag') {
          key.push(this.$route.params[x])
        }
      })
      return key.join('/')
    }
  },
  methods: {
    popAlert (type, message) {
      this.alert.type = type
      this.alert.message = message
    },
    changeListCondition () {
      if (this.listingCondition === 'card') {
        this.listingCondition = 'list'
      } else {
        this.listingCondition = 'card'
      }
    },
    resetFilterInfo (filterInfo, sortCondition, list) {
      this.filterInfo = filterInfo || {
        categories: [],
        stars: [],
        amenity: [],
        maxPrice: '',
        minPrice: ''
      }
      this.sortCondition = sortCondition || 'BEST'
      this.hotelList.param = ''
      this.hotelList.searchTime = ''
      this.hotelList.list = []
      this.hotelList.totalCount = 0
      this.hotelIndexReset(0)
      this.mapList = []
      this.selectedHotel = {}
      this.scrollContentNum = 5
      this.lastScrollTop = 0
      this.listingCondition = list || 'list'
    },
    hotelIndexChange (num) {
      this.hotelIndex += num
    },
    hotelIndexReset (num) {
      this.hotelIndex = parseInt(num, 10)
    },
    changeSelectedHotel (hotelInfo) {
      this.selectedHotel = hotelInfo
    },
    openPop (flag) {
      const objParam = this.$route.params
      objParam.popFlag = flag
      this.$router.push({
        params: objParam
      })
      this.popping = 'open'
      setTimeout(() => {
        this.popping = false
        this.historyLength += 1
      }, 150)
    },
    closePop () {
      if (!this.popping) {
        this.popping = 'close'
        setTimeout(() => {
          this.popping = false
          if (this.historyLength > 0) {
            window.history.back(1)
            this.historyLength -= 1
          } else {
            const objParams = this.$route.params
            if (objParams.popFlag) {
              objParams.popFlag = ''
              this.$router.replace({
                params: objParams
              })
            } else {
              this.$router.replace('/')
            }
          }
        }, 150)
      }
    },
    bodyScroll (e) {
      if (this.lastScroll < window.pageYOffset) {
        this.scrollDown = true
        if (window.pageYOffset > 50) {
          this.topOut = true
        } else {
          this.topOut = false
        }
      } else {
        this.scrollDown = false
        this.topOut = false
      }
      if (window.pageYOffset <= 0) {
        this.scrollDown = false
      }
      this.lastScroll = window.pageYOffset
    },
    lastScrollTopChange (resp) {
      this.lastScrollTop = resp || 0
    },
    scrollContentNumIncrease () {
      this.scrollContentNum += 5
    }
  },
  mounted () {
    window.EventBus.$on('popAlert', this.popAlert)
    this.lastScroll = window.pageYOffset
    this.resetFilterInfo()
    // const deviceInfo = DeviceInfo.deviceInfo(window)
    // console.log(deviceInfo)
    window.addEventListener('scroll', this.bodyScroll)
    document.getElementById('beforeLoading').style.display = 'none'
    document.getElementById('beforeLoading2').style.display = 'none'
  },
  destroyed () {
    window.removeEventListener('scroll', this.bodyScroll)
  },
  created () {
    const pUserAgentString = navigator.userAgent
    const lUaMatches = pUserAgentString.match(/(.*?)\/(.*?)\s\((.*?)\)(.*)/i)
    if (lUaMatches) {
      const key = lUaMatches[4]
      if (key.match(/wmpapps\/([0-9]+)\.([0-9]+)\.?([0-9]+)?/i)) { // 위메프 앱
        this.iswmpapp = true
        this.isFromApp = true
      } else if (key.match(/wshop\/([0-9]+)\.([0-9]+)\.?([0-9]+)?/i)) { // 원더쇼핑 앱
        this.isFromApp = true
      }
    }
  }
}
</script>

<style scoped>
  .hide{
    position: absolute;
    height: 0;
    width: 0;
    left: -9999px;
    overflow: hidden;
  }
</style>

<style>
  @import url('https://fonts.googleapis.com/css?family=Roboto+Slab');
  @import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
  #wmpHotel{
    color: #333;
    overflow: hidden;
  }
  #wmpHotel .scrollable{
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
  }
  #wmpHotel > div.page{
    height: 100%;
    position: relative;
    padding-top: 48px;
  }
  #wmpHotel .roboto{
    font-family: 'Roboto Slab', serif !important;
  }
  #wmpHotel * {
    box-sizing: border-box;
    touch-action: manipulation;
    font-family: 'Noto Sans KR', 'Apple SD Gothic Neo', sans-serif;
  }
  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  button {
    padding: 0;
    background: none;
    border: 0;
    cursor: pointer;
    user-select: none;
  }
  button:focus{
    outline: none;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .2s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



